import { ResponsiveLine } from "@nivo/line";
import { useState, useEffect } from 'react';
import { useTheme } from "@mui/material";
import { tokens } from "../theme";
import {api} from "../utils/api";

const LineChartWeight = ({ isCustomLineColors = false, isDashboard = false }) => {
    const [chartData, setChartData] = useState(Array);
    const dayNames = ["SO", "MO", "DI", "MI", "DO", "FR", "SA"];

    useEffect(() => {
        const fetchWeightData = async () => {
            try {
                const response = await api.get('/get-weight.php');
                const weightData = response.data.map(entry => ({
                    x: dayNames[new Date(entry.date).getDay()], // Converts the date string to a Date object
                    y: entry.weight
                }));

                const goalWeightData = response.data.map(entry => ({
                    x: dayNames[new Date(entry.date).getDay()], // Converts the date string to a Date object
                    y: entry.goal_weight
                }));

                setChartData([
                    { id: 'Gewicht', data: weightData, color: tokens("dark").blueAccent[300] },
                    { id: 'Zielgewicht', data: goalWeightData, color: tokens("dark").greenAccent[500] }
                ]);
            } catch (error) {
                alert(error.response.data.message);
            }
        };

        if (sessionStorage.getItem("role").toString().toLowerCase() !== "coach" || sessionStorage.getItem("sus_id")) fetchWeightData();
    }, []);


    const theme = useTheme();
    const colors = tokens(theme.palette.mode);

    if (!chartData) {
        return <div>Loading...</div>;
    }

    return (
        <ResponsiveLine
            data={chartData}
            theme={{
                axis: {
                    domain: {
                        line: {
                            stroke: colors.grey[100],
                        },
                    },
                    legend: {
                        text: {
                            fill: colors.grey[100],
                        },
                    },
                    ticks: {
                        line: {
                            stroke: colors.grey[100],
                            strokeWidth: 1,
                        },
                        text: {
                            fill: colors.grey[100],
                        },
                    },
                },
                legends: {
                    text: {
                        fill: colors.grey[100],
                    },
                },
                tooltip: {
                    container: {
                        color: colors.primary[500],
                    },
                },
            }}
            colors={isDashboard ? { datum: "color" } : { scheme: "nivo" }} // added
            margin={{ top: 50, right: 110, bottom: 50, left: 60 }}
            xScale={{ type: "point" }}
            yScale={{
                type: "linear",
                min: "auto",
                max: "auto",
                stacked: false,
                reverse: false,
            }}
            yFormat=" >-.2f"
            curve="catmullRom"
            axisTop={null}
            axisRight={null}
            axisBottom={{
                orient: "bottom",
                tickSize: 0,
                tickPadding: 5,
                tickRotation: 0,
                legend: isDashboard ? undefined : "Monate", // added
                legendOffset: 36,
                legendPosition: "middle",
            }}
            axisLeft={{
                orient: "left",
                tickValues: 5, // added
                tickSize: 3,
                tickPadding: 5,
                tickRotation: 0,
                legend: isDashboard ? undefined : "in kg", // added
                legendOffset: -40,
                legendPosition: "middle",
            }}
            enableGridX={false}
            enableGridY={false}
            pointSize={8}
            pointColor={{ theme: "background" }}
            pointBorderWidth={2}
            pointBorderColor={{ from: "serieColor" }}
            pointLabelYOffset={-12}
            useMesh={true}
            legends={[
                {
                    anchor: "bottom-right",
                    direction: "column",
                    justify: false,
                    translateX: 100,
                    translateY: 0,
                    itemsSpacing: 0,
                    itemDirection: "left-to-right",
                    itemWidth: 80,
                    itemHeight: 20,
                    itemOpacity: 0.75,
                    symbolSize: 12,
                    symbolShape: "circle",
                    symbolBorderColor: "rgba(0, 0, 0, .5)",
                    effects: [
                        {
                            on: "hover",
                            style: {
                                itemBackground: "rgba(0, 0, 0, .03)",
                                itemOpacity: 1,
                            },
                        },
                    ],
                },
            ]}
        />
    );
};

export default LineChartWeight;